import React, { useState } from "react";
import "./App.css";
import video from './video/clickbait.mp4'
import audio from './video/clickbait.mp3'

function App() {
  const [showVideo, setShowVideo] = useState(false);
  const [isHovered, setIsHovered] = useState(false);

  const handleClick = () => {
    setShowVideo(true);
  };

  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };

  return (
    <div className="container">
      <div className="center">
        <button
          className="button"
          onClick={handleClick}
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
        >
          НАЖМИ НА МЕНЯ
          <span></span>
        </button>
        <div className={`shine-overlay ${isHovered ? "active" : ""}`}></div>
      </div>
      {showVideo ? (
        <div className="backgroundVideoWrapper">
          <video autoPlay loop className="backgroundVideo" muted>
            <source
              src={video}
              type="video/mp4"
            />
            Your browser does not support the video tag.
          </video>
          <audio autoPlay loop>
            <source
              src={audio}
              type="audio/mp3"
            />
            Your browser does not support the audio element.
          </audio>
        </div>
      ) : null}
    </div>
  );
}

export default App;
